import styled from 'styled-components';

export const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  grid-row-gap: 24px;
  width: 100%;
  height: 100%;
  padding: 24px;
`;

export const Content = styled.div`
  display: flex;
  flex-direction: column;
  grid-row-gap: 14px;
  width: 100%;
  height: fit-content;
  overflow-x: auto;
`;

export const EmptyText = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
`;
