import { FC, useEffect, useMemo } from 'react';
import * as ST from './styled';
import { AppLoader, PageHeader } from '@src/components';
import { useModalControls, useTags } from '@src/hooks';
import { CreateTagModal } from '@pages/Tags/CreateTagModal';
import { TagItem } from '@pages/Tags/TagItem';



const Tags: FC = () => {
  const { isOpen, open, close } = useModalControls();
  const { getTags, tags, isLoading } = useTags();

  useEffect(() => {
    getTags();
  }, []);

  const renderedTags = useMemo(() => {
    if (isLoading) {
      return (
        <AppLoader />
      )
    }

    if (!tags.length) {
      return (
        <ST.EmptyText>
          Тегов нет
        </ST.EmptyText>
      );
    }

    return (
      <ST.Content>
        {tags.map(it => <TagItem key={it.id} tag={it} refetch={getTags} />)}
      </ST.Content>
    )
  }, [tags, isLoading]);

  return (
    <ST.Wrapper>
      <PageHeader
        title="Теги"
        buttonText="Создать тег"
        count={`${tags.length} тегов`}
        onButtonClick={open}
      />
      {renderedTags}
      {isOpen && (
        <CreateTagModal close={close} refetch={getTags} />
      )}
    </ST.Wrapper>
  )
}

export default Tags;
