import React, { PropsWithChildren } from 'react';
import { Icon, IconTypes } from '@components/Icon';
import { ButtonProps, ButtonVariant, Modal } from '@src/components';

import * as ST from './styled';

export enum DialogTypes {
  ANSWER = 'answer',
  NOTIFICATION = 'notification',
  WARNING = 'warning',
  INFO = 'info'
}

type ActionType = React.ButtonHTMLAttributes<HTMLButtonElement> & ButtonProps;

interface IActionsTypes {
  ok?: ActionType;
  cancel?: ActionType;
  submit?: ActionType;
}

interface IDialogProps {
  loading?: boolean;
  classNamePrefix?: string;
  className?: string;
  title?: string;
  type?: DialogTypes;
  onConfirm?: () => void;
  onCancel?: () => void;
  onClose?: () => void;
  onOk?: () => void;
  actions?: IActionsTypes;
  closeOnDocumentClick?: boolean;
  closeOnEscape?: boolean;
}

const dialogIcons: Record<DialogTypes, IconTypes> = {
  answer: 'circle-warning',
  notification: 'circle-information',
  warning: 'warning',
  info: 'circle-information',
};

interface IProps extends IDialogProps, PropsWithChildren {
  closeHandler: () => void;
}

const Dialog: React.FC<IProps> = ({
  loading,
  className,
  title,
  type,
  closeHandler,
  onConfirm,
  onOk,
  actions,
  children,
}) => (
  <Modal
    open
    closeOnDocumentClick={false}
    onClose={closeHandler}
  >
    <ST.Container className={className}>
      <ST.Close
        onClick={closeHandler}
      >
        <Icon
          type="close"
        />
      </ST.Close>
      <ST.Body>
        {type && (
          <ST.StyledIcon className={type}>
            <Icon type={dialogIcons[type]} />
          </ST.StyledIcon>
        )}
        {title && <ST.Title>{title}</ST.Title>}
        <ST.Content>{children}</ST.Content>
      </ST.Body>
      <ST.Buttons>
        {onConfirm ? (
          <>
            <ST.StyledButton
              {...actions?.submit}
              variant={type === DialogTypes.WARNING ? ButtonVariant.DANGER : ButtonVariant.FILLED}
              onClick={onConfirm}
              isLoading={loading}
              disabled={loading}
            />
            <ST.StyledButton
              {...actions?.cancel}
              id="ConfirmDialogCancel"
              variant={ButtonVariant.OUTLINE}
              onClick={closeHandler}
              disabled={loading}
            />
          </>
        ) : (
          <ST.StyledButton
            onClick={onOk}
            isLoading={loading}
            {...actions?.ok}
          />
        )}
      </ST.Buttons>
    </ST.Container>
  </Modal>
);

export { Dialog };
