import { FC, useEffect, useState } from 'react';
import { Button, ButtonVariant, Input, SideModal } from '@src/components';
import * as ST from './styled';
import { useFormik } from 'formik';
import { TagsApi } from '@src/api/Tags/TagsApi';
import { toastError, toastSuccess } from '@src/utils/toast';
import { ITag } from '@src/api/Tags/models';
import * as yup from 'yup';

interface IProps {
  tag: ITag;
  close: () => void;
  refetch: () => void;
}

interface TagForm {
  value: string;
}

let VALIDATION_SCHEMA = yup.object().shape({
  value: yup.string().required('Обязательное поле'),
});

export const UpdateTagModal: FC<IProps> = ({ close, refetch, tag }) => {
  const [isLoading, setIsLoading] = useState(false);

  const form = useFormik<TagForm>({
    initialValues: {
      value: tag.value,
    },
    validationSchema: VALIDATION_SCHEMA,
    onSubmit: async (values) => {
      try {
        setIsLoading(true);
        await TagsApi.fetchUpdateTag(tag.id, values.value);
        toastSuccess(`Тег ${values.value} успешно обновлен`)
        setIsLoading(false)
        close();
        await refetch();
      } catch (e) {
        toastError(e)
      } finally {
        setIsLoading(false)
      }
    },
  });

  return (
    <SideModal
      titleText="Изменение тега"
      onClose={close}
      closeOnDocumentClick={false}
    >
      <ST.Form onSubmit={form.handleSubmit}>
        <Input
          name="value"
          onChange={form.handleChange}
          value={form.values.value}
          onBlur={form.handleBlur}
          caption="Название тега"
          placeholder="Введите название тега"
          isError={Boolean(form.errors.value)}
          errorText={form.errors.value}
        />
        <ST.Footer>
          <Button text="Сохранить" fullWidth isLoading={isLoading} />
          <Button variant={ButtonVariant.OUTLINE} text="Отмена" fullWidth onClick={close} />
        </ST.Footer>
      </ST.Form>
    </SideModal>
  )
}
