import styled from 'styled-components';

export const Form = styled.form`
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100%;
  height: fit-content;
  grid-row-gap: 24px;
`;

export const Footer = styled.div`
  margin-top: auto;
  display: flex;
  align-items: center;
  grid-column-gap: 24px;
`;
