import { FC, useState } from 'react';
import { ITag } from '@src/api/Tags/models';
import * as ST from './styled';
import { IconSize } from '@components/Icon';
import { useModalControls } from '@src/hooks';
import { Dialog, DialogTypes } from '@components/Dialog';
import { toastError, toastSuccess } from '@src/utils/toast';
import { TagsApi } from '@src/api/Tags/TagsApi';
import { UpdateTagModal } from '@pages/Tags/UpdateTagModal';

interface IProps {
  refetch: () => void;
  tag: ITag;
}

export const TagItem: FC<IProps> = ({ tag, refetch }) => {
  const [isLoading, setIsLoading] = useState(false);
  const { isOpen, open, close } = useModalControls();
  const updateModal = useModalControls();

  const handleDelete = async () => {
    try {
      setIsLoading(true);
      await TagsApi.fetchDeleteTag(tag.id);
      refetch();
      close();
      toastSuccess(`Тег ${tag.value} успешно удален`);
    } catch (e) {
      toastError(e);
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <ST.Wrapper>
      <ST.Info>
        {tag.value} ({tag.id})
      </ST.Info>
      <ST.Controls>
        {tag.canDeleteEdit && <ST.EditIcon type={'edit'} size={IconSize.XL} onClick={updateModal.open} />}
        {tag.canDeleteEdit && <ST.DeleteIcon type={'delete'} size={IconSize.XL} onClick={open} />}
      </ST.Controls>
      {isOpen && (
        <Dialog
          type={DialogTypes.WARNING}
          closeHandler={close}
          onConfirm={handleDelete}
          actions={{
            cancel: {
              text: 'Отменить',
              onClick: close,
            },
            submit: {
              isLoading,
              text: 'Удалить',
            }
          }}
        >
          Вы действительно хотите удалить тег {tag.value}?
        </Dialog>
      )}
      {updateModal.isOpen && (
        <UpdateTagModal tag={tag} close={updateModal.close} refetch={refetch} />
      )}
    </ST.Wrapper>
  )
}
